.drawer-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: 1rem;
  isolation: isolate;
}

.drawer-container {
  background: #FFFFFF;
  border-radius: 12px;
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  animation: drawerSlideIn 0.3s ease-out;
}

@keyframes drawerSlideIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.drawer-header {
  background: #D4AF5F;;
  color: white !important;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #e5e7eb;
}

.header-content {
  position: relative;
  display: flex;
  align-items: center;
  color: white !important;
  min-height: 40px;
}

.drawer-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: white !important;
  margin: 0;
  flex: 1;
}

/* LTR Layout: Title on left */
[dir="ltr"] .drawer-title {
  text-align: left;
}

/* RTL Layout: Title on right */
[dir="rtl"] .drawer-title {
  text-align: right;
}

.close-btn {
  position: absolute;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 6px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  transform: translateY(-50%);
}

/* LTR Layout: Title centered, Close button on right */
[dir="ltr"] .close-btn {
  right: 0;
}

/* RTL Layout: Title centered, Close button on left */
[dir="rtl"] .close-btn {
  left: 0;
}

.close-btn:hover {
  background: rgba(95, 100, 109, 0.1);
  color: #5F646D;
}

.drawer-content {
  padding: 2rem;
  overflow-y: auto;
  max-height: calc(90vh - 100px);
}

.user-avatar-section {
  text-align: center;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #e5e7eb;
}

.avatar-container {
  margin-bottom: 1rem;
}

.user-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #e5e7eb;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.avatar-placeholder {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #f3f4f6;
  border: 3px solid #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #9ca3af;
  margin: 0 auto;
}

.user-name {
  font-size: 1.25rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 0.5rem 0;
}

.user-email {
  color: #6b7280;
  margin: 0;
  font-size: 0.875rem;
}

.form-section {
  margin-bottom: 2rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.form-group label {
  font-weight: 600;
  color: #374151;
  font-size: 0.875rem;
}

.form-input {
  padding: 0.75rem;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.2s;
  font-family: inherit;
  width: 100%;
}

.form-input:focus {
  outline: none;
  border-color: #BFAB86;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

.form-input.disabled {
  background: #f9fafb;
  color: #6b7280;
  cursor: not-allowed;
}

.form-help {
  font-size: 0.875rem;
  color: #6b7280;
}

.error-message {
  color: #dc2626;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

.roles-display {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.role-badge {
  background: #158638;
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 999px;
  font-size: 0.875rem;
  font-weight: 500;
}

.no-roles {
  color: #6b7280;
  font-style: italic;
}

.department-display {
  padding: 0.75rem;
  background: #f9fafb;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  color: #6b7280;
}

.department-value {
  display: block;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.status-toggle {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 0.5rem;
}

.toggle-switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
}

.toggle-input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 24px;
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

.toggle-input:checked + .toggle-slider {
  background-color: #2BA149;
}

.toggle-input:checked + .toggle-slider:before {
  transform: translateX(26px);
}

.status-text {
  font-weight: 500;
  color: #374151;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  padding-top: 1.5rem;
  border-top: 1px solid #e5e7eb;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
  font-size: 0.875rem;
  min-width: 100px;
}

.btn-primary {
  background: #BFAB86;
  color: #FFFFFF;
}

.btn-primary:hover:not(:disabled) {
  background: #B8941F;
  transform: translateY(-1px);
}

.btn-primary:disabled {
  background: #9ca3af;
  cursor: not-allowed;
  transform: none;
}

.btn-secondary {
  background: transparent;
  color: #1B1D21 !important;
  border: 1px solid #1B1D21;
  font-weight: 600 !important;
}

.btn-secondary:hover {
  background: #1B1D21;
  color: #FFFFFF !important;
}

/* RTL Support */
.drawer-container.rtl {
  direction: rtl;
}

.drawer-container.rtl .form-actions {
  justify-content: flex-start;
}

/* Responsive */
@media (max-width: 650px) {
  .drawer-overlay {
    z-index: 99999999999 !important;
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
  }

  .drawer-container {
    margin: 1rem;
    max-height: calc(100vh - 2rem);
    z-index: 99999999999 !important;
    position: relative !important;
  }

  .drawer-content {
    padding: 1.5rem;
    overflow-y: auto;
    max-height: calc(100vh - 200px);
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .form-actions {
    flex-direction: column;
  }

  .btn {
    width: 100%;
  }
}

@media (max-width: 430px) {
  .drawer-overlay {
    z-index: 99999999999 !important;
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
  }

  .drawer-container {
    margin: 0.5rem;
    max-height: calc(100vh - 1rem);
    z-index: 99999999999 !important;
    position: relative !important;
    max-width: calc(100vw - 1rem);
  }

  .drawer-content {
    padding: 1rem;
    overflow-y: auto;
    max-height: calc(100vh - 150px);
  }

  .drawer-header {
    padding: 1rem 1.5rem;
  }

  .drawer-title {
    font-size: 1.25rem;
  }
}

/* Ensure modal appears above all elements including sidebar */
@media (max-width: 650px) {
  .drawer-overlay {
    z-index: 99999999999 !important;
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    width: 100vw !important;
    height: 100vh !important;
    isolation: isolate !important;
    transform: translateZ(0) !important;
    will-change: transform !important;
  }

  .drawer-container {
    z-index: 99999999999 !important;
    position: relative !important;
    isolation: isolate !important;
    transform: translateZ(0) !important;
  }
}

/* Force sidebar to be below modal on mobile/tablet */
@media (max-width: 650px) {
  .sidebar {
    z-index: 1 !important;
  }
  
  /* Additional specificity to override any other z-index */
  .drawer-overlay ~ * .sidebar,
  .drawer-overlay ~ * * .sidebar,
  .drawer-overlay ~ * * * .sidebar {
    z-index: 1 !important;
  }
}

/* Global override for when modal is open */
@media (max-width: 650px) {
  body:has(.drawer-overlay) .sidebar {
    z-index: 1 !important;
  }
  
  body:has(.drawer-overlay) .drawer-overlay {
    z-index: 99999999999 !important;
  }
}
